@font-face {
  font-family: calculatorFont;
  src: url("MesloLGS_NF_Italic.b319002f.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e4e7f4;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.calculator {
  aspect-ratio: 5 / 6;
  background-color: #857f7f;
  border: 1px solid #000;
  grid-template-rows: 1fr 5fr;
  grid-template-columns: 1fr;
  height: 80%;
  margin-inline: auto;
  display: grid;
}

.calculator .screen-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.calculator .screen-container button {
  text-align: right;
  border: 1px solid #000;
  width: 100%;
  height: 100%;
  padding: 0 min(.8vw, 1.6vh);
  font-family: calculatorFont;
  font-size: min(4.5vw, 5vh);
  font-weight: bolder;
}

.calculator .buttons {
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(5, 1fr);
  display: grid;
}

.calculator .buttons button {
  border: 1px solid #000;
  font-size: min(2.5vw, 5vh);
}

.calculator .buttons .purple-background {
  background-color: #c7d2ff;
}

.calculator .buttons .green-background {
  background-color: #7bf756;
}

.calculator .buttons .button-clear {
  background-color: orange;
  border-color: red;
}

.calculator .buttons .button-clear:hover {
  cursor: pointer;
  background-color: #ef3939;
}

.calculator .buttons .change-on-hover:hover {
  cursor: pointer;
  background-color: #fee48f;
  border-color: #b18907;
}

